.flip-card-container {
  width: 100%;
  height: 100%;
  perspective: 1000px;
  position: fixed;
  z-index: 10;
}

.flip-card-container.scrolled {
  height: 80vh;
  width: 20vw;
  position: fixed;
  top: 3vh;
  left: 3vw;
  z-index: 1000;
}

.flip-card {
  transition: transform 0.8s;
  transform-style: preserve-3d;
  will-change: transform;
}

.flip-card-container.flipped .flip-card {
  transform: rotateY(180deg);
}

.flip-card-front,
.flip-card-back {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  backface-visibility: hidden;
}

.flip-card-back {
  transform: rotateY(180deg);
}

@media (max-width: 768px) {
  .flip-card {
    width: 90vw;
  }
  /* Small phone / touch devices */
  @media (max-width: 420px), (hover: none) and (pointer: coarse) {
    .flip-card-container.scrolled {
      width: 96vw;
      left: 2vw;
      height: 9vh;
      top: 2vh;
    }

    .flip-card-front,
    .flip-card-back {
      height: 9vh;
      padding: 0 8px;
      gap: 8px;
    }

    .flip-card-front .content,
    .flip-card-back .content {
      font-size: 0.9rem;
    }
  }
}
